            <div class="article-detail">
                <p>
                    50
                    
                </p>
                <p>1.</p>
                <p>Stray birds of summer come to my window to sing and fly away. And yellow 
                        leaves of autumn, which have no songs, flutter and fall there with a sign.
                </p>
                <p>2.</p>
                <p>A Troupe of little vagrants of the world, leave your footprints in my 
                        words.</p>
                <p>3.</p>
                <p>The world puts off its mask of vastness to its lover. It becomes small as 
                    one song, as one kiss of the eternal.</p>
                <p>4.</p>
                <p>It is the tears of the earth that keep here smiles in bloom.</p>
                <p>5.</p>
                <p>The mighty desert is burning for the love of a bladeof grass who shakes 
                        her head and laughs and flies away.</p>
                <p>6.</p>
                <p>If you shed tears when you miss the sun, you also miss the stars.</p>
                <p>7.?</p>
                <p>The sands in your way beg for your song and your movement, dancing water. 
                        Will you carry the burden of their lameness?</p>
                <p>8.</p>
                <p>Her wishful face haunts my dreams like the rain at night.</p>
                <p>9.</p>
                <p>Once we dreamt that we were strangers. We wake up to find that we were dear 
                        to each other.</p><p>10.</p>
                <p>Sorrow is hushed into peace in my heart like the evening among the silent 
                        trees.</p>
                <p>11.()()</p>
                <p>Some unseen fingers, like an idle breeze, are playing upon my heart the 
                        music of the ripples.</p>
                <p>12.&ldquo;?&rdquo;</p>
                <p>&ldquo;&rdquo;</p><p>&ldquo;?&rdquo;</p>
                <p>&ldquo;&rdquo;</p><p>What language is thine, O sea?</p>
                <p>The language of eternal question. What language is thy answer, O sky?</p>
                <p>The language of eternal silence.</p>
                <p>13.</p>
                <p>Listen, my heart, to the whispers of the world with which it makes love to 
                        you.</p>
                <p>14.--</p>
                <p>The mystery of creation is like the darkness of night--it is great. 
                        Delusions of knowledge are like the fog of the morning.</p>
                <p>15.</p>
                <p>Do not seat your love upon a precipice because it is high.</p>
                <p>16.</p>
                <p>I sit at my window this morning where the world like a passer-by stops for 
                        a moment, nods to me and goes.</p>
                <p>17.();</p>
                <p>There little thoughts are the rustle of leaves; they have their whisper of 
                joy in my mind.</p><p>18.</p>
                <p>What you are you do not see, what you see is your shadow.</p>
                <p>19.</p>
                <p>My wishes are fools, they shout across thy song, my Master. Let me but 
                listen.</p>
                <p>20.</p>
                <p>I cannot choose the best. The best chooses me.</p>
                <p>21.</p>
                <p>They throw their shadows before them who carry their lantern on their back.
                </p>
